*{
  box-sizing: border-box;
  margin:0;
}

main{
  padding:1rem;
}

.home{
  background-color: rgb(49, 153, 148);
}

.away{
  background-color: slategray;
}

.top-nav{
  display:grid;
  grid-auto-flow: column;
  justify-content: center;
  gap:0.5rem;
  background-color: darkgray;
}

.top-nav a{
  color:white;
  text-decoration: none;
  padding:1rem 2rem;
}